import React, { useContext } from 'react';

import { TranslationsContext } from '../App';
import './index.scss';
import { ITranslations } from 'src';

interface IProps {
  currentMode: string;
  onModeChange: (mode: 'units' | 'amenities' | 'floors') => void;
  isViewingAmenity: boolean;
  onGalleryToggle: (state: boolean) => void;
  isViewingGallery: boolean;
  hideModeButtons: boolean;
  disableGallery: boolean;
}

type Props = IProps;

const ModeButtons: React.SFC<Props> = ({
  currentMode,
  onModeChange,
  isViewingAmenity,
  isViewingGallery,
  onGalleryToggle,
  hideModeButtons,
  disableGallery,
}) => {
  const translations: ITranslations = useContext(TranslationsContext);

  return (
    <div className="mode-buttons-container">
      {isViewingAmenity ? (
        <>
          {disableGallery ? (
            <></>
          ) : (
            <button
              className={`mode-button ${isViewingGallery && 'active'}`}
              onClick={() => onGalleryToggle(!isViewingGallery)}>
              {isViewingGallery ? 'Close' : 'Open'} Gallery
            </button>
          )}
        </>
      ) : (
        <>
          {!hideModeButtons && (
            <>
              <button
                className={`mode-button ${
                  currentMode === 'amenities' && 'active'
                }`}
                onClick={() => onModeChange('amenities')}>
                {translations.buildingsAmenities}
              </button>
              <button
                className={`mode-button ${currentMode === 'units' && 'active'}`}
                onClick={() => onModeChange('units')}>
                3D Unit Search
              </button>
            </>
          )}
        </>
      )}
    </div>
  );
};

export default ModeButtons;
